<template>
	<section class="section main">
  <loading :active.sync="isLoading" 
        :can-cancel="true" 
        ></loading>
     <div class="container-fluid">
       <div class=" frm">
    <form class="login-form">
      <input type="email"  placeholder="Enter Registered Email"  v-model="email"/>
      <input type="text" placeholder="Enter  Username" v-model="username"/>
      <button @click.prevent="sendPost()">RESET PASSWORD</button>
    </form>
  </div>
     </div>
	</section>
</template>
<script>
import { mapState, mapActions } from 'vuex';
import axios from 'axios';
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

export default {
    components: { Loading },

	data(){
    return {
      isLoading: false,  
      username:'',
      email:''
    }
	},
	methods: {
	async	sendPost() {
      this.showModal = false;
      this.isLoading = true;
 let sendRequest = await axios.post('https://middlewarez.herokuapp.com/api/v1/auth/game/forgot?task=forgot', {
    username: this.username,
    email: this.email
  })

  setTimeout(() => {
        this.isLoading = false;
      }, 1000);

  console.log(sendRequest.data.data.result_desc);
      
        this.$swal({
          type: "success",
          title: sendRequest.data.data.result_desc,
          showConfirmButton: true
        });
        return;
      
}
  },
	computed: {
    ...mapState(['loginError'])
	}
}
</script>

<style>

.frm {
  margin-top: 120px !important;
  margin-bottom: 180px !important;
  
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

::-webkit-input-placeholder {
   text-align: center;
}

:-moz-placeholder { /* Firefox 18- */
   text-align: center;  
}

::-moz-placeholder {  /* Firefox 19+ */
   text-align: center;  
}

:-ms-input-placeholder {  
   text-align: center; 
}

.login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
}

.frm  input {
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.frm  button {
  background: #4CAF50;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.frm  button:hover,.frm  button:active,.frm  button:focus {
  background: #43A047;
}
.frm  .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.frm  .message a {
  color: #4CAF50;
  text-decoration: none;
}
.frm  .register-form {
  display: none;
}

.container:before, .container:after {
  content: "";
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #EF3B3A;
}

</style>
